.search-container{
    display: flex;
    justify-content: center;
    margin: 10px;
    margin-left: 80px;
}
.search-input{
    padding: 8px 10px;
    font-size: 15px;
    width: 250px;
    border-width: 0;
    background-color: #a592f2;
    color: #e7e9f0;
    border-radius: 50px;
    box-shadow: 0 0 5px rgba(255 255 255 .5) ;
}
.search-input ::placeholder{
    color: #e7e9f0;
}